.categories {
  display: flex;
  flex-direction: row;
  gap: 70px;
  align-items: center;
  padding: 69px 150px;
  background-color: #F8F8F9;

  .buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &__title {
      @include title;
    }

    &__arrows {
      display: flex;
      gap: 10px;
      margin-top: 20px;
      margin-bottom: 40px;

      .arrow {
        width: 30px;
        height: 30px;
        background-color: white;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }

    &__view {
      @include button;
      padding-bottom: 5px;
      width: max-content;
      border: 2px solid transparent;
      cursor: pointer;
      transition: border-bottom 0.5s;
    }

    &__view:hover {
      border-bottom: 2px solid #88B13E;
    }
  }

  .list {
    display: flex;
    gap: 30px;

    .item {
      display: flex;
      flex-direction: column;
      gap: 15px;

      &__title {
        @include button;
        font-size: 18px;
        align-self: center;
      }

      &__image {
        height: 150px;
        width: 150px;
        object-fit: cover;
      }
    }
  }
}
